.header {
  color: #fff;
  background: red;
  width: 26rem;
  padding: 1rem;
  text-align: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.36);
  border-radius: 5px;
  font-size: 15px;
}

.formshape {
  width: 40%;
  position: relative;
  left: 55rem;
  bottom: 9rem;
}

form {
  background: #fff;
  width: 28rem;
  height: 27rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 12px;
}

input {
  padding: 0.7rem;
  width: 24rem;
  background: #ffffff;
  border: 1px solid #dadada;
  border-radius: 4.5px;
}

.selection {
  width: 25.5rem;
  background: #ffffff;
  border: 1px solid #dadada;
  border-radius: 4.5px;
}

.inpshape button {
  width: 25.5rem;
  height: 3rem;
  background: #b5b5b5;
  box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.46);
  border-radius: 2px;
  color: #ffffff;
  font-family: math;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
}

.inpshape button:enabled {
  background: #37cc8a;
  box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.46);
}
.inpshape {
  padding: 1rem;
}

.terms {
  font-size: 14px;
  font-family: emoji;
}
